@import url(https://fonts.googleapis.com/css2?family=Titillium+Web&display=swap);
body {
  font-family: 'Titillium Web', sans-serif;
  margin: auto;
  color: #999;
  background-color: rgb(47, 47, 47);
}

a {
  font: large;
  color: #dffff0;
  text-decoration: none;
  font-weight: bolder;
}
.title {
  display: flexbox;
  text-align: center;
  width: 1080px;
  margin: auto;
  margin-top: 10px;
  border: 1px solid #777;
}

.title:hover {
  background-color: #333;
  color: #777;
  border: 1px solid #222;
}

.author {
  text-align: right;
  display: block;
  color: #ddd;
  margin-left: auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border: 1px solid #555;
  padding: 2px 5px 2px 5px;
}

.author:hover {
  background-color: #222;
  color: #fff;
  border: 1px solid #777;
}

h1 {
  font-size: 2em;
}
/*# sourceMappingURL=App.css.map */
.controls {
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  display: block;
  text-align: center;
}

.button {
  margin-left: 2px;
  width: 130px;
  background-color: #555;
  border: 1px dashed #777;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 15px;
  /* Safari */
  transition-duration: 0.4s;
  text-transform: uppercase;
  overflow: hidden;
  cursor: grab;
  font-family: 'Titillium Web', sans-serif;
}

.button:hover {
  background-color: #777;
}

.button:active {
  background-color: #aaa;
}

.button#btnClear {
  background-color: #991010;
}

.button#btnClear:hover {
  background-color: #ff1b1b;
}

.button#btnClear:active {
  background-color: #aa1b1b;
}

.button#btnRun {
  background-color: #7ac99b;
  color: black;
}

.button#btnRun:hover {
  background-color: #a8f5c8;
}

.button#btnStop {
  background-color: #c99e7a;
  color: black;
}

.button#btnStop:hover {
  background-color: #f7c8a2;
}

input {
  width: 50px;
  margin: 0px 5px 0px 5px;
  text-align: center;
  background-color: #444;
  color: white;
}

input:focus {
  outline: none;
  border-color: #a8ff1b;
  box-shadow: 1px 2px 6px #51be07;
}

input#iterations {
  color: #95f395;
  font-size: larger;
  border: none;
}

input#interval {
  color: #95ddf3;
  font-size: large;
  border: none;
  margin-right: 0;
}
/*# sourceMappingURL=Game.css.map */
.Board {
  position: relative;
  background-color: #111;
  margin: 10px auto;
  border: 1px solid #888;
  background-image: linear-gradient(#444 1px, transparent 1px),
    linear-gradient(90deg, #333 1px, transparent 1px);
}

.Cell {
  background: #ababab;
  position: absolute;
}

